import Vue from 'vue'
import Router from 'vue-router'
import { routers } from './router'
import store from '../store/index'
import bus from '../store/modules/bus/index'
Vue.use(Router)
console.log(process.env.BASE_URL, 'llllllllllllll')
const router = new Router({
  mode: 'history',
  base: '/test',
  routes: routers
})

router.beforeEach((to, form, next) => {
  // let token = store.state.token || window.localStorage.getItem('sessionId')
  // console.log(to)
  // if (token || to.name === 'login') {
  //   if (to.name === 'home') {
  //     !store.state.bus && store.registerModule('bus', bus)
  //   }
  //   next()
  // } else {
  //   next({
  //     path: '/login'
  //     // query: { redirect: to.fullPath }
  //   })
  // }
  let igNoreArr = ['paySuccess', 'orderConfirm', 'orderList', 'orderAdmin', 'commodityClass']
  if (igNoreArr.indexOf(to.name) > -1) {
    next()
  }
  if (to.query.code || store.state.sessionId || (to.name === 'login' && to.query.openid)) {
    next()
  }
  setTitle(to.meta.title)
})

export function setTitle (title : any) {
  title = title || ''
  window.document.title = title
}

export default router
