import * as types from './mutation-types'
import Vue from 'vue'

export default {
  [types.SET_TOKEN] (state : any, sessionId : any) {
    state.token = sessionId
    window.localStorage.setItem('sessionId', sessionId)
  },
  [types.SET_USER_INFO] (state : any, userInfo : any) {
    Vue.set(state, 'userInfo', userInfo)
  },
  [types.SET_SESSION_ID] (state : any, sessionId : any) {
    state.sessionId = sessionId
  },
  [types.SET_HOUSE_LIST] (state : any, houseList : any) {
    Vue.set(state, 'houseList', houseList)
  },
  [types.COMMODITY_USER_INFO] (state : any, commodityUserInfo : any) {
    state.isUserInfoBack = true
    Vue.set(state, 'commodityUserInfo', commodityUserInfo)
  },
  [types.SET_SELECT_COMMODITY_LIST] (state : any, {type, item}) {
    let arr = []
    let id = item['id']
    let isExist = false
    state.selectCommodityList.forEach((obj) => {
      if (obj.id === item.id) {
        isExist = true
        obj.num = item['num']
        if (type === 'add') {
          arr.push(obj)
        } else {
          if (obj.num > 0) {
            arr.push(obj)
          }
        }
      } else {
        arr.push(obj)
      }
    })
    if (type === 'add') {
      if (!isExist) {
        arr.push(item)
      }
    }
    console.log(arr)
    Vue.set(state, 'selectCommodityList', arr)
  }
}
